<template>
  <div id="app">
    <NaviBar @navi-to="naviTo" v-bind:items="titles"/>
    <GridLayout  v-if="activeTab == 0 || activeTab ==1" v-bind:title="titles[activeTab]" 
                 v-bind:dataSource = "dataSource"/>
    <div v-else>{{ titles[activeTab] }}</div>
  </div>
</template>

<script>
import NaviBar from './components/NaviBar.vue'
import GridLayout from './components/GridLayout.vue'
import * as loader from './utils/dataLoader.js'

export default {
  name: 'app',
  data: () => {
    return {
      titles: loader.tabs,
      activeTab: 0,
      dataSource: loader[loader.tabs[0]]
    }
  },
  components: {
    NaviBar, GridLayout
  },
  methods: {
    naviTo: function(i) {
      this.activeTab = i;
      this.dataSource = loader[loader.tabs[i]];
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F4F4F4;

  display: flex;
  flex-direction: column;
  align-items: center;
}
</style>
