<template>
    <div class="NaviBar">
        <div class="NaviBar-content">
            <div v-for="(item, i) in items" :key="item" v-on:click="naviTo(i)" style="margin: 0px 20px 0px 20px" 
                 v-bind:class="{'initial': activeTab !== i, 'is-active': activeTab === i}">
                 {{ item }}
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'NaviBar',
  props: {
      items: { type: Array, default: () => ['UX', 'Me'] }
  },
  data: function() {
      return {
          activeTab: 0
      }
  },
  methods: {
      naviTo: function(i) {
          this.activeTab = i;
          this.$emit('navi-to', i); 
      }
  }
}
</script>

